$totes-slots-edit-padding-top: 72px;
$totes-slots-edit-margin-bottom: 20px;
$totes-slots-edit-item-height: $input-height + $totes-slots-edit-margin-bottom;

.rcs-tote-edit-slot {
  display: grid;
  position: relative;
  grid-template-columns: 64px 1fr;
  grid-column-gap: 24px;
  padding-left: 72px;

  &__title {
    position: absolute;
  }

  &__labels {
    padding-top: $totes-slots-edit-padding-top;
  }

  &__label {
    @extend %rcs-typo-capation__sm;
    text-align: end;
    height: $totes-slots-edit-item-height;
    padding-top: 8px;
  }

  &__form {
    position: relative;
    padding: 16px 20px;
    padding-top: $totes-slots-edit-padding-top;
    width: 100%;
    background-color: $color-primary-50;
    border-top-right-radius: $x-border-radius-md;
    border-bottom-right-radius: $x-border-radius-md;
    border-bottom-left-radius: $x-border-radius-md;

    &:before {
      content: '';
      position: absolute;
      right: 100%;
      top: 0;
      border: 16px solid $color-primary-50;
      border-left-color: transparent;
      border-bottom-color: transparent;
    }

    > * {
      margin-bottom: $totes-slots-edit-margin-bottom;
    }
  }

  &__infobox {
    @extend %rcs-typo-body__md;
    display: flex;
    align-items: center;
    height: 52px;
    padding: 0 16px;
    border: $x-border-width solid $x-border-color-pale;
    border-radius: $x-border-radius-md;
    background-color: $color-white;
    margin-bottom: $totes-slots-edit-margin-bottom;
    cursor: not-allowed;
  }

  &__empty {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: $totes-slots-edit-padding-top;
  }
}
