%rcs-table-info-label {
  font-size: 11px;
  font-weight: 700;
  color: $color-neutral-500;
}

.rcs-orders {
  &__order-info-cell {
    display: flex;
    position: relative;
    flex-direction: column;
    min-height: 44px;
    margin: 16px 0;
    padding: 8px 32px 8px 16px;
    border: $x-border-width transparent solid;
    border-radius: $x-border-radius-md;

    :last-of-type {
      margin-bottom: 0;
    }

    &--expandable {
      border-color: $x-border-color;
      background-color: $color-white;
    }
  }

  &__order-info-item {
    display: grid;
    grid-template-columns: 3fr 56px 56px 56px 4fr;
    margin-bottom: 16px;
    justify-items: center;
  }

  &__order-info-sku {
    width: 100%;
    align-content: baseline;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    white-space: nowrap;

    span {
      @extend %rcs-table-info-label;
    }
  }

  &__order-info-tote {
    justify-self: end;
    white-space: nowrap;

    span {
      @extend %rcs-table-info-label;
    }
  }

  &__order-info-arrow {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 32px;
    width: 32px;
    top: 0;
    right: 0;
    cursor: pointer;
    transition: $x-transition-default;
    user-select: none;

    &--open {
      transform: rotate(180deg);
    }

    > * {
      height: $x-arrow-height-md;
    }
  }
}
