@import 'ToteEdit/Slot';

.rcs-tote-edit {
  &__actions {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 36px;
  }

  &__close-icon {
    margin-left: 50px;
    cursor: pointer;
  }

  &__general-info {
    display: grid;
    grid-template-columns: 120px 232px 187px 187px 256px;
    grid-gap: 20px;
    padding-bottom: 62px;
  }

  &__slots {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 72px;
  }
}
