.rcs-totes {
  &__slot-cell {
    display: flex;
    flex-direction: column;
    width: 256px;
    height: 132px;
    margin: 16px 0;
    padding: 8px 16px;
    border: $x-border-width solid $x-border-color;
    border-radius: $x-border-radius-md;
    background-color: $color-white;

    &--empty {
      justify-content: center;
      align-items: center;
    }

    > div {
      margin-bottom: 4px;
    }
  }
}
