.rcs-totes {
  &__filters-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
  }

  &__filters-presets {
    display: flex;
    align-items: center;

    .rcs-select {
      min-width: 256px;
    }
  }

  &__filters-presets-title {
    font-size: $x-font-size-md;
    margin: 0 16px;
    white-space: nowrap;
  }

  &__filters-fields {
    display: grid;
    grid-template-columns: 3fr 2fr 2fr 2fr 2fr 3fr 600px;
    width: 100%;

    > * {
      flex-grow: 1;
      margin-right: 8px;
    }

    > :last-child {
      margin-right: 0;
    }
  }

  &__filters-fields-buttons {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;

    > * {
      margin-bottom: 0;
    }

    :first-child {
      margin: 8px 0;
    }
  }
}
