.rcs-user {
  height: 100%;
  position: relative;
  border-left: $x-border-width $x-border-color-pale solid;
  padding-left: 16px;
  user-select: none;

  &__data {
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__image {
    width: 32px;
    height: 32px;
    border-radius: 32px;
    object-fit: contain;
  }

  &__name {
    font-size: 14px;
    font-weight: $x-font-weight-semibold;
    padding: 0 16px;
  }

  &__dropdown {
    padding: 16px 16px;
  }
}
