.rcs-delivery {
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: center;
  flex-grow: 1;
  overflow: hidden;

  h3 {
    text-align: center;
    margin-bottom: 100px;
  }

  .rcs-label {
    width: 100%;
    &__label {
      @extend %rcs-typo-body__md;
    }
  }

  .rcs-select {
    font-size: 24px;
    height: 68px;
    text-transform: capitalize;
  }

  &__content {
    width: 100%;
    max-width: 1000px;
    overflow: auto;
    flex-grow: 1;
    margin-top: 64px;
  }

  &__filters {
    margin-top: 32px;
  }

  &__products {
    flex-grow: 1;
    margin-top: 32px;
  }

  &__button {
    position: absolute;
    right: 24px;
    bottom: 24px;
  }
}
