// Colors
$color-white: #fff;
$color-black: #1d1d1d;

$color-primary-500: #0da36a;
$color-primary-400: #2fc48b;
$color-primary-300: #a8d4c5;
$color-primary-200: #c9e6dc;
$color-primary-100: #ebf5f1;
$color-primary-50: #f3f8f6;

$color-neutral-900: #292b2d;
$color-neutral-800: #484d50;
$color-neutral-500: #656b6e;
$color-neutral-300: #c1d1ce;
$color-neutral-100: #EBEBEB;
$color-neutral-50: #f7f7f7;

$color-secondary-500: #e83056;
$color-secondary-200: #ffe9ee;

$color-tertiary-500: #007aff;
$color-tertiary-200: #e9f3ff;

$color-accent-900: #f13700;
$color-accent-800: #ff5c00;
$color-accent-500: #ff6f01;
$color-accent-400: #fc932d;
$color-accent-200: #ffe1c4;

$x-background-color-default: white;

// Accent states
$x-accent-gradient-default: linear-gradient(to right, $color-accent-400, $color-accent-500);
$x-accent-gradient-active: linear-gradient(to right, $color-accent-800, $color-accent-900);

// Borders
$x-border-color: $color-primary-300;
$x-border-color-pale: $color-primary-200;
$x-border-radius-md: 5px;
$x-border-radius-lg: 10px;
$x-border-width: 2px;

// Dimension
$x-element-height-md: 52px;
$x-arrow-height-md: 9px;
$x-padding-table-cell: 26px 0;
$tables-space-right: 32px;

// Paddings
$x-padding-section-side: 32px;

// Fonts
$x-font-weight-regular: 400;
$x-font-weight-semibold: 500;
$x-font-weight-bold: 700;

$x-font-size-lg: 19px;
$x-font-size-md: 15px;
$x-font-size-sm: 13px;

// Effects
$x-transition-default: 100ms all ease-in;
$x-box-shadow-1: 0px 8px 20px #023c2612;

// Z-indexes
$x-z-index-dropdown: 100;
