$button-lg-width: 228px;
$button-lg-height: 72px;

$button-md-width: 194px;
$button-md-height: $x-element-height-md;

$button-sm-width: 134px;
$button-sm-height: 44px;

.rcs-button {
  position: relative;
  border: none;
  background: none;
  color: $color-white;
  font-weight: $x-font-weight-bold;
  transition: $x-transition-default;
  outline: none;
  cursor: pointer;
  margin: 8px 0 8px 8px;

  &:disabled {
    opacity: 0.7;
    cursor: not-allowed;
  }

  // Common form buttons styles
  &__primary,
  &__ghost,
  &__secondary {
    width: $button-md-width;
    height: $button-md-height;
    border: solid $x-border-width transparent;
    border-radius: $x-border-radius-md;
    font-size: $x-font-size-md;

    &:active {
      border-color: $color-neutral-800;
      background-color: $color-neutral-800;
    }
  }

  // Styles for form buttons
  &__primary {
    border-color: $color-primary-500;
    background-color: $color-primary-500;
  }

  &__ghost {
    color: $color-primary-500;
    border-color: $x-border-color-pale;
    background-color: $color-white;
  }

  &__secondary {
    border-color: $color-secondary-500;
    background-color: $color-secondary-500;
  }

  // Styles for general purpose buttons
  &__accent,
  &__neutral {
    width: $button-lg-width;
    height: $button-lg-height;
    font-size: $x-font-size-lg;
    border-radius: calc(#{$button-lg-height} / 2);
  }

  &__accent {
    background: $x-accent-gradient-default;

    &:after {
      content: '';
      position: absolute;
      left: 5%;
      top: 10%;
      width: 90%;
      height: 100%;
      background: transparent $x-accent-gradient-default 0% 0% no-repeat padding-box;
      border-radius: 36px;
      opacity: 0.4;
      filter: blur(10px);
      z-index: -1;
    }

    &:active {
      background: $x-accent-gradient-active;

      &:after {
        background: transparent $x-accent-gradient-active 0% 0% no-repeat padding-box;
      }
    }
  }

  &__neutral {
    background: $color-neutral-800;

    &:active {
      background: $color-black;
    }
  }

  // Styles for table buttons
  &__table {
    width: $button-sm-width;
    height: $button-sm-height;
    margin-top: 0;
    margin-bottom: 0;
    color: $color-black;
    font-size: $x-font-size-md;
    font-weight: $x-font-weight-semibold;
    border: solid $x-border-width $color-primary-200;
    border-radius: $x-border-radius-md;
    background-color: $color-white;

    &:active {
      background: $color-primary-50;
    }
  }
}
