.rcs-supply-loading {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 512px;

  &__header {
    padding-bottom: 140px;
  }

  &__products {
    padding-bottom: 152px;
    text-align: center;
  }

  &__products-line {
    display: flex;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 68px;
    font-size: 24px;
    padding: 24px 30px;
    margin: 24px;
    border-radius: $x-border-radius-md;
    background-color: $color-primary-100;
  }
}