.rcs-tote-slot-resupply {
  flex: 1 1 0px;
  max-width: 440px;
  min-width: 440px;

  &--disabled {
    opacity: 0.5;
    user-select: none;
    pointer-events: none;

    .rcs-tote-slot-resupply__confirm,
    .rcs-tote-slot-resupply__image-title {
      display: none;
    }

    .rcs-tote-slot-resupply__image {
      background-image: none !important;
      border: solid $x-border-width $x-border-color-pale;
    }
  }

  &__image {
    position: relative;
    height: 280px;
    width: 440px;
    margin-bottom: 20px;
    border-radius: $x-border-radius-md;
    box-shadow: $x-box-shadow-1;
    background-position: center;
    background-size: cover;
  }

  &__image-title {
    position: absolute;
    padding: 11px 22px;
    top: 15%;
    left: -20px;
  }

  &__barcode {
    width: 100%;
    margin-bottom: 24px;
  }

  &__confirm {
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }
}
