.rcs-login {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 5fr 4fr;

  &__lead {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  &__lead-logo {
    height: 320px;
    width: 320px;
    background: url('../assets/images/logo.png');
    background-position: center;
    background-size: contain;
    margin-bottom: 100px;
  }

  &__lead-slogan {
    width: 60%;
  }

  &__user {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $color-primary-50;
  }

  &__user-header {
    margin-bottom: 72px;
  }

  &__user-name {
    margin-bottom: 36px;
    width: 500px;
  }

  &__user-password {
    margin-bottom: 92px;
    width: 500px;
  }
}
