$tote-resupply-padding-side: 100px;

.rcs-tote-resupply {
  flex-grow: 1;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;

  &__content {
    padding: 60px $tote-resupply-padding-side;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &__submit {
    margin: 0 $tote-resupply-padding-side 60px;
    align-self: flex-end;
    z-index: 1;
  }
}
