.rcs-number-input {
  position: relative;

  input[type='number'] {
    -webkit-appearance: textfield;
    -moz-appearance: textfield;
    appearance: textfield;
  }
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
  }

  &__element {
    text-align: center;
    padding-left: 40px;
    padding-right: 40px;
  }

  &__plus,
  &__minus {
    top: 8px;
    width: 36px;
    height: 36px;
    position: absolute;
    padding: 10px;
    border: none;
    border-radius: $x-border-radius-md;
    background-color: $color-primary-50;
    cursor: pointer;
  }

  &__minus {
    left: 8px;
  }

  &__plus {
    right: 8px;
  }
}
