.rcs-progress-bar {
  display: flex;
  flex-direction: column;
  align-items: center;

  span {
    margin-bottom: 30px;
  }

  &__wrapper{
    position: relative;
    height: 24px;
    width: 360px;
    border: solid $color-white 4px;
    box-shadow: 0px 1px 3px #00000078, inset 0px 1px 4px #0000002B;
    border-radius: 36px;
    background-color: $color-neutral-100;
  }

  &__loader {
    height: 100%;
    width: 100%;
    background-image: $x-accent-gradient-default;
    border-radius: 36px;
    box-shadow: inset 0px 1px 1px #0000002B;
    transition: max-width 200ms ease-out;
  }
}