.rcs-filter {
  display: flex;
  justify-content: space-between;

  &__label {
    @extend %rcs-typo-body__md;
    white-space: nowrap;
    align-self: flex-end;
    padding: 12px 20px 12px 0;
    margin: 8px 8px 8px 0;
  }

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  &__item {
    padding: 12px 20px;
    margin: 8px 0 8px 8px;
    background-color: $color-primary-50;
    border-radius: $x-border-radius-md;
    cursor: pointer;
    transition: $x-transition-default;

    &:active {
      color: $color-black;
      background-color: $color-primary-200;
    }

    &--checked {
      background-color: $color-primary-500;
      color: $color-white;
    }

    input {
      display: none;
    }
  }
}
