.rcs-supply-nav-actions {
  @extend %rcs-typo-body__md;
  display: flex;
  align-items: center;

  &__link,
  &__stop {
    height: 100%;
    display: flex;
    align-items: center;
  }

  &__link {
    padding: 0px 25px;
  }

  &__stop {
    padding: 0px 52px;
    border-left: solid $x-border-width $x-border-color-pale;
    background-color: $color-primary-50;
    margin-left: 25px;
  }
}