.rcs-loader {
  width: 100%;
  height: 100%;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  &__element {
    width: 50px;
    height: 50px;
    border: 4px solid $color-primary-500;
    border-radius: 50%;
    animation: spin 1.2s cubic-bezier(0.25, 0.45, 0.75, 0.55) infinite;
    position: relative;

    &:before,
    &:after {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      border-top: 4px solid $color-primary-500;
      border-right: 4px solid $color-primary-500;
      position: absolute;
      top: 1px;
      left: -5px;
      box-shadow: 4px -4px 0 4px #fff;
    }

    &:after {
      top: 24px;
      left: 35px;
      border: none;
      border-bottom: 4px solid $color-primary-500;
      border-left: 4px solid $color-primary-500;
      box-shadow: -4px 4px 0 4px #fff;
    }
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
