.rcs-table-head {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    display: block;
    width: 100%;
    top: 100%;
    left: 0;
    border-bottom: $x-border-width solid $color-primary-200;
  }

  &__cell {
    @extend %rcs-typo-capation__md;
    white-space: nowrap;
    padding: $x-padding-table-cell;
    text-align: left;
  }
}
