$step-padding-side: 60px;

.rcs-steps {
  min-height: 132px;
  max-height: 132px;
  width: 100%;
  border-bottom: $x-border-width $x-border-color-pale solid;
  display: flex;
  transition: $x-transition-default;

  &__step {
    position: relative;
    flex: 1 1 0px;
    padding: 39px $step-padding-side;
    border-right: $x-border-width $x-border-color-pale solid;
    transition: $x-transition-default;

    &:first-of-type {
      padding-left: 120px;
      .rcs-steps__step-icon {
        left: $step-padding-side;
      }
    }

    &:last-of-type {
      padding-left: 120px;
      border-right: none;
    }

    &--active {
      .rcs-steps__step-icon--active {
        opacity: 1;
      }
    }

    &--done {
      background-color: $color-primary-100;

      .rcs-steps__step-icon--done {
        opacity: 1;
      }
    }

    &--todo {
      & > * {
        opacity: 50%;
      }

      .rcs-steps__step-icon--todo {
        opacity: 1;
      }
    }
  }

  &__step-title {
    color: $color-primary-500;
    margin: 0;
    margin-bottom: 13px;
  }

  &__step-subtitle {
    @extend %rcs-typo-capation__sm;
    color: $color-neutral-500;
  }

  &__step-icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
    opacity: 0;
  }
}
