.rcs-navigation {
  height: 100%;
  display: inline-flex;
  font-size: $x-font-size-md;
  align-items: center;

  &__element {
    display: flex;
    align-items: center;
    margin: 0 16px;
    border: calc(#{$x-border-width} + 1px) solid transparent;
    margin-bottom: -3px;
    height: 100%;
    transition: $x-transition-default;

    &--active,
    &:hover {
      border-bottom-color: $color-secondary-500;
      font-weight: $x-font-weight-semibold;
    }
  }
}
