@font-face {
  font-family: GraublauWeb;
  font-weight: 400;
  src: url('../assets/fonts/NotoSansJP-Regular.otf') format('opentype');
}

@font-face {
  font-family: GraublauWeb;
  font-weight: 500;
  src: url('../assets/fonts/NotoSansJP-Medium.otf') format('opentype');
}

@font-face {
  font-family: GraublauWeb;
  font-weight: 700;
  src: url('../assets/fonts/NotoSansJP-Bold.otf') format('opentype');
}

body {
  font-family: 'Noto Sans JP', sans-serif;
  font-size: $x-font-size-lg;
  font-weight: $x-font-weight-regular;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: $x-font-size-lg;
  color: $color-black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
  font-size: 40px;
  font-weight: $x-font-weight-regular;
  letter-spacing: -0.4px;
}

h2 {
  font-size: 28px;
  font-weight: $x-font-weight-regular;
  letter-spacing: -0.3px;
}

h3 {
  font-size: 28px;
  font-weight: $x-font-weight-regular;
  letter-spacing: -0.3px;
}

h4 {
  font-size: 24px;
  font-weight: $x-font-weight-regular;
}

%rcs-typo-subtitle {
  &__lg {
    font-size: $x-font-size-lg;
    font-weight: $x-font-weight-bold;
  }

  &__md {
    font-size: $x-font-size-md;
    font-weight: $x-font-weight-bold;
  }
}

%rcs-typo-body {
  &__lg {
    font-size: $x-font-size-lg;
    font-weight: $x-font-weight-regular;
  }

  &__md {
    font-size: $x-font-size-md;
    font-weight: $x-font-weight-regular;
  }
}

%rcs-typo-capation {
  &__md {
    font-size: $x-font-size-sm;
    font-weight: $x-font-weight-bold;
  }

  &__sm {
    font-size: $x-font-size-sm;
    font-weight: $x-font-weight-regular;
  }
}
