%rcs-table-filters-background {
  padding: 16px $tables-space-right;
  border-radius: $x-border-radius-lg;
  background-color: $color-primary-50;
  margin-bottom: 32px;
}

%rcs-table-action-buttons {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 16px;

  > :last-child {
    margin-right: $tables-space-right;
  }
}

%rcs-input {
  width: 100%;
  padding: 0 16px;
  border: solid $color-primary-200 $x-border-width;
  border-radius: $x-border-radius-md;
  font-size: $x-font-size-md;
  outline: none;

  &:focus {
    border-color: $x-border-color;
    box-shadow: $x-box-shadow-1;
  }
}
