.rcs-navbar {
  display: flex;
  min-height: 64px;
  border-bottom: $x-border-color-pale solid $x-border-width;
  padding: 0 $x-padding-section-side;
  justify-content: flex-end;

  &--navigation-only {
    padding: 0 calc(3 * #{$x-padding-section-side});
  }

  .rcs-breadcrumbs {
    flex-grow: 1;
  }
}
