.rcs-service-issue-details {
  margin: 96px 0;
  width: 930px;
  align-self: center;

  &__table {
    border-bottom: solid $x-border-width $color-primary-200;
  }

  &__issue-info {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 32px;
    min-height: 196px;
  }

  &__actions {
    margin-top: 52px;
    display: flex;
    justify-content: center;
  }
}
