$select-min-width: 284px;
$select-height: $x-element-height-md;
$select-elements-padding: 16px;

.rcs-select {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: $select-height;
  width: 100%;
  background-color: $color-white;
  border: $x-border-width solid $color-primary-200;
  border-radius: $x-border-radius-md;
  font-size: $x-font-size-md;
  user-select: none;

  &--open {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    box-shadow: $x-box-shadow-1;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 $select-elements-padding;
    height: 100%;
    width: 100%;
  }

  &__placeholder {
    color: $color-neutral-500;
  }

  &__arrow {
    height: $x-arrow-height-md;

    &--open {
      transform: rotate(180deg);
    }
  }

  &__menu {
    position: absolute;
    min-width: calc(100% + 2 *#{$x-border-width});
    max-width: 200%;
    max-height: 340px;
    left: -#{$x-border-width};
    top: 100%;
    background-color: $color-white;
    border: $x-border-width solid $color-primary-200;
    border-bottom-left-radius: $x-border-radius-md;
    border-bottom-right-radius: $x-border-radius-md;
    box-shadow: $x-box-shadow-1;
    z-index: $x-z-index-dropdown;
    overflow: auto;
  }

  &__menu-item {
    display: flex;
    align-items: center;
    min-height: 64px;
    width: 100%;
    padding: 8px $select-elements-padding;
    cursor: pointer;
    user-select: none;

    &:hover {
      background-color: $color-neutral-50;
    }
  }
}
