.rcs-supply-confirmation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__icons {
    position: relative;
    margin-bottom: 72px;
  }

  &__icon-confirm {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  h2 {
    margin-bottom: 25px;
  }

  h4 {
    margin-top: 0;
    margin-bottom: 96px;
  }
}