@import 'bootstrap';

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

body {
  width: 1920px;
  height: 1080px;
  margin: 0 auto;
  // For development purpose. Shows dimensions of screen
  //border: 1px solid black;
  position: relative;
}

#root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: unset;
}
