.rcs-dropdown {
  display: flex;
  align-items: center;
  height: 100%;

  &__menu {
    position: absolute;
    display: none;
    right: 2%;
    top: 90%;
    min-width: 100%;
    box-shadow: $x-box-shadow-1;
    z-index: $x-z-index-dropdown;
    background-color: $x-background-color-default;

    &--open {
      display: block;
    }
  }

  &__arrow {
    transition: $x-transition-default;
    height: $x-arrow-height-md;

    &--open {
      transform: rotate(180deg);
    }
  }
}
