$checkbox-size: 32px;

.rcs-checkbox {
  display: inline-flex;
  align-items: center;

  &__label {
    font-size: $x-font-size-md;
    user-select: none;
  }

  &__input-element {
    display: none;
  }

  &__tick {
    margin: 0 24px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: $checkbox-size;
    height: $checkbox-size;
    border: $x-border-width solid $color-primary-500;
    border-radius: $x-border-radius-md;
    background-color: $color-white;
    user-select: none;
  }
}
