.rcs-supply-progress-preview {
  display: flex;
  overflow: hidden;
  position: relative;

   &__items {
     min-width: 720px;
     max-width: 720px;
     overflow: auto;
   }

  &__items-header {
    display: flex;
    justify-content: space-between;
    padding: 37px 28px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 226px;

    h3 {
      margin-bottom: 100px;
    }

    h4 {
      margin: 0;
      margin-bottom: 24px;
    }
  }

  &__continue {
    position: absolute;
    bottom: 90px;
    right: 100px;
  }
}