.rcs-item-select {
  &__item {
    display: flex;
    position: relative;
    height: 68px;
    padding: 22px 28px;
    background-color: $color-primary-100;
    border-radius: $x-border-radius-md;
    justify-content: space-between;
    font-size: 24px;
    margin: 6px 0;
    cursor: pointer;
    transition: $x-transition-default;

    &:active {
      color: $color-black;
      background-color: $color-primary-200;
    }

    &--checked {
      background-color: $color-primary-500;
      color: $color-white;
    }
  }

  input {
    display: none;
  }
}
