%confirmation-absolute-elements {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 568px;
  min-height: 40px;
  text-align: center;
  background-color: $color-white;
  padding: 0px 24px;
}

.rcs-confirmation {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;


  &__container {
    position: relative;
    min-width: 800px;
    max-width: 800px;
    min-height: 494px;
    height: 494px;
    border: 11px solid;

    &--confirm {
      border-color:  $color-primary-200;
    }

    &--error {
      border-color: $color-secondary-500;
    }
  }

  &__header {
    @extend %confirmation-absolute-elements;
    top: -65px;
  }

  &__content {
    padding: 60px 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
  }

  &__buttons {
    @extend %confirmation-absolute-elements;
    display: flex;
    justify-content: space-between;
    bottom: -50px;
  }
}